.backgroundWall{
	background-position: center;
	background-repeat: repeat-y;
	padding-bottom: 120px;
	min-height: 700px;
}
.backgroundBeach{
	background-image: -webkit-gradient(linear,left top, left bottom,from(#FFFFFF),color-stop(#F7ECC0),color-stop(#F4F8C0),color-stop(#D4B682), to(#8F6215));
	background-image: -webkit-linear-gradient(#FFFFFF,#F7ECC0,#F4F8C0,#D4B682, #8F6215);
	background-image: -o-linear-gradient(#FFFFFF,#F7ECC0,#F4F8C0,#D4B682, #8F6215);
	background-image: linear-gradient(#FFFFFF,#F7ECC0,#F4F8C0,#D4B682, #8F6215);
}
.backgroundHome{
	background: -webkit-gradient(linear, left top, left bottom, from(white), color-stop(skyblue), to(skyblue));
	background: -webkit-linear-gradient(white, skyblue, skyblue);
	background: -o-linear-gradient(white, skyblue, skyblue);
	background: linear-gradient(white, skyblue, skyblue);
}
.backgroundMountain{
	background: -webkit-gradient(linear,left top, left bottom,from(#FFFFFF),color-stop(#959994),color-stop(#85EB6C), to(#3CE013));
	background: -webkit-linear-gradient(#FFFFFF,#959994,#85EB6C, #3CE013);
	background: -o-linear-gradient(#FFFFFF,#959994,#85EB6C, #3CE013);
	background: linear-gradient(#FFFFFF,#959994,#85EB6C, #3CE013);
}
.containerSticky{
	position: -webkit-sticky;
	position: sticky;
	top:25px;
	margin-top: -30px;
	z-index: 1;
	height: 120px;
}



