
.navbar-fixed{
	position: -webkit-sticky;
	position: sticky;
	left:0px;
  	top:0px;
  	z-index:2;
}

.navbar-white{
	background-color: white; 

	border:5px solid black;
}
.navbrand{
	font-size: 24px;
	color:black;
}
.navbrand:hover{
	color: black;
}
.margin_top-1px{
	margin-top: -1px;
}
.navlink{
	font-size: 18px;
	color:black;
}

.navlink:hover{
	color:black;
	background: -webkit-gradient(linear, left top, left bottom, from(#AEF57F), to(#95BD86)) !important;
	background: -webkit-linear-gradient(#AEF57F, #95BD86) !important;
	background: -o-linear-gradient(#AEF57F, #95BD86) !important;
	background: linear-gradient(#AEF57F, #95BD86) !important;
}
.navlink:focus{
	color:black;
	background: -webkit-gradient(linear, left top, left bottom, from(#AEF57F), to(#95BD86)) !important;
	background: -webkit-linear-gradient(#AEF57F, #95BD86) !important;
	background: -o-linear-gradient(#AEF57F, #95BD86) !important;
	background: linear-gradient(#AEF57F, #95BD86) !important;
}


